import React, { useState, useEffect } from "react";
import {
    MapContainer,
    TileLayer,
    Polyline,
    Popup,
    useMap,
    useMapEvents,
} from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-arrowheads"; // Import the plugin

import BusStopMarker from "./BusStopMarker";

import {
    route1,
    route2,
    route3,
    route4,
    route5,
    route6,
} from "./bus routes/BusRoutes";

// Leaflet supports the following colors: ‘beige’, ‘black’, ‘blue’, ‘cadetblue’, ‘darkblue’, ‘darkgreen’, ‘gray’, ‘darkpurple’, ‘darkred’, ‘green’, ‘lightblue’, ‘lightgray’, ‘lightgreen’, ‘lightred’, ‘orange’, ‘pink’, ‘purple’, and ‘red’.
// Route 1 = "blue";
// Route 2 = "black";
// Route 3 = "brown";
// Route 4 = "darkgreen";
// Route 5 = "purple";
// Route 6 = "red";

const busRouteColors = ["blue", "black", "brown", "darkgreen", "purple", "red"];

const MapPassive = () => {
    const [popupInfo, setPopupInfo] = useState(null);
    const [zoomLevel, setZoomLevel] = useState(14); // Initial zoom level
    const [activeRouteIndex, setActiveRouteIndex] = useState(null); // Tracks the active route

    const handlePolylineClick = (e, route) => {
        const { lat, lng } = e.latlng; // Get the clicked location
        setPopupInfo({
            position: [lat, lng],
            info: route.info,
        });
    };

    const handleRouteHover = (index) => {
        setActiveRouteIndex(index);
    };

    const handleRouteHoverOut = () => {
        setActiveRouteIndex(null);
    };

    const ZoomListener = () => {
        useMapEvents({
            zoomend: (e) => {
                setZoomLevel(e.target.getZoom());
            },
        });
        return null;
    };

    const AddArrowheads = () => {
        const map = useMap();

        useEffect(() => {
            // Add arrowheads to all polylines
            map.eachLayer((layer) => {
                if (layer instanceof L.Polyline) {
                    layer.arrowheads({
                        size: "10px", // Arrow size
                        frequency: "250px", // Distance between arrows
                        fill: true, // Fill the arrows
                        color: layer.options.color, // Arrow color matches Polyline
                    });
                }
            });
        }, [map]);

        return null;
    };

    return (
        <MapContainer
            center={[29.2804352098, -94.805943501]}
            zoom={14}
            style={{ height: "100vh", width: "100%" }}
        >
            <ZoomListener />
            <AddArrowheads />

            <TileLayer
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                attribution="&copy; <a href='https://www.openstreetmap.org/copyright'>OpenStreetMap</a> contributors"
            />

            {/* ================== Draws all bus routes ================== */}
            {[route1, route2, route3, route4, route5, route6].map(
                (route, index) => (
                    <Polyline
                        key={index}
                        positions={route.route}
                        color={busRouteColors[index]}
                        eventHandlers={{
                            click: (e) => handlePolylineClick(e, route),
                            mouseover: () => handleRouteHover(index), // Highlight route
                            mouseout: handleRouteHoverOut, // Reset on hover out
                        }}
                    />
                )
            )}
            {/* ================== End of Draws all bus routes ================== */}

            {/* ================== Marks bus stops for the active route ================== */}
            {activeRouteIndex !== null &&
                [route1, route2, route3, route4, route5, route6][
                    activeRouteIndex
                ].stops.map((stop, index) => (
                    <BusStopMarker
                        key={index}
                        stop={stop}
                        zoomLevel={zoomLevel}
                    />
                ))}
            {/* ================== End of Marks bus stops ================== */}

            {popupInfo && (
                <Popup
                    position={popupInfo.position}
                    onClose={() => setPopupInfo(null)}
                >
                    {popupInfo.info}
                </Popup>
            )}
        </MapContainer>
    );
};

export default MapPassive;
