import React from "react";
import { Marker, Popup } from "react-leaflet";
import L from "leaflet";
import smallBusStopPin from "../../assets/pin_icons/small-bus-icon.png";
import largeBusStopPin from "../../assets/pin_icons/bus-stop-pin.png";

const BusStopMarker = ({ stop, zoomLevel }) => {
    // Select the icon size based on zoom level
    const icon = L.icon({
        iconUrl: zoomLevel > 15 ? largeBusStopPin : smallBusStopPin,
        iconSize: zoomLevel > 14 ? [20, 20] : [15, 15], // Adjust sizes
        iconAnchor: [10, 20], // Anchor to ensure proper placement on the map
        popupAnchor: [5, -10], // Adjust for popup positioning
    });

    return (
        <Marker position={stop.coords} icon={icon}>
            <Popup>{stop.name}</Popup>
        </Marker>
    );
};

export default BusStopMarker;
